body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.search-bar-container {
    margin-top: 30px;
    padding-top: 50px;
    background-color: white;
    border-radius: 10px;
    width: inherit;
}

.slogan-text {
    margin-top: 20px;
    font-size: 40px;
    text-shadow: 0 3px 5px rgba(0, 0, 0, 0.5);
}

::-webkit-input-placeholder { /* WebKit browsers */
    color: transparent;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
   color: transparent;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
   color: transparent;
}
:-ms-input-placeholder { /* Internet Explorer 10+ */
   color: transparent;
}

textarea::-webkit-input-placeholder { /* WebKit browsers */
    color: transparent;
}
textarea:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
   color: transparent;
}
textarea::-moz-placeholder { /* Mozilla Firefox 19+ */
   color: transparent;
}
textarea:-ms-input-placeholder { /* Internet Explorer 10+ */
   color: transparent;
}

@media only screen and (max-width: 700px) {
    .slogan-text {
        margin-top: 10px;
        font-size: 30px;
    }
}

@media only screen and (max-width: 500px) {
    .slogan-text {
        font-size: 23px;
    }
}
.brand-logo img {
    width: 40px;
    margin-bottom: -5px;
    margin-right: 5px;
}

@media only screen and (max-width: 414px) {
    .brand-logo img {
        width: 30px;
    }
    nav .brand-logo {
        font-size: 25px;
    }
}
.map-container { 
    width: 100vw;
    height: 85vh;
}

.map-loading-text {
    color: white;
    font-size: 50px;
}

.map-error-text {
    color: white;
    font-size: 30px;
}

.marker img {
    width: 40px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    transition: -webkit-transform 0.25s cubic-bezier(0.485, 1.65, 0.545, 0.835) 0s;
}

.marker img:hover {
    cursor: pointer;
    -webkit-transform: translate(-50%, -50%) translate3d(0px, 0px, 0px) scale(1.6, 1.6);
            transform: translate(-50%, -50%) translate3d(0px, 0px, 0px) scale(1.6, 1.6);
    transition: -webkit-transform 0.25s cubic-bezier(0.485, 1.65, 0.545, 0.835) 0s;
}

@media only screen and (max-width: 580px) {
    .marker img {
        width: 25px;
    }
}
.about-us-container h2 {
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.about-us-des {
    font-size: 25px;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}
.app-container {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100vw;
  height: 100vh;
}

.background-image {
  opacity: 0.7;
  width: 100vw;
  height: 100vh;
}

.carousel-item {
  margin: 0;
  -webkit-transform: none !important;
          transform: none !important;
}

.carousel .carousel-item>img {
  width: inherit;
  image-rendering: optimizeSpeed;
}

.page{
  top: 0;
  width: -webkit-fill-available;
  position: absolute;
  background: none;
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.page-footer{
  padding-bottom: 20px;
}

.main{
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

@media screen and (max-width: 768px) {
  .background-image {
    width: 200vw;
  }
  
  .carousel-item {
    margin-left: -50vw;
  }
}
